<template>
    <h2>後台產品列表</h2>
    <div class="container">
        <div class="text-end mt-4">
          <button class="btn btn-primary" @click="toggleProductModal('create')">
            建立新的產品
          </button>
        </div>
        <table class="table mt-4">
          <thead>
            <tr>
              <th width="120">
                分類
              </th>
              <th>產品名稱</th>
              <th width="120">
                原價
              </th>
              <th width="120">
                售價
              </th>
              <th width="100">
                是否啟用
              </th>
              <th width="120">
                編輯
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item) in products" :key = "item.id">
              <td>{{item.category}}</td>
              <td>{{item.title}}</td>
              <td>{{item.origin_price}}</td>
              <td>{{item.price}}</td>
              <td>
                <span class="text-success" v-if="item.is_enabled === 1">啟用</span>
                <span v-else>未啟用</span>
              </td>
              <td>
                <div class="btn-group">
                  <button type="button" class="btn btn-outline-primary btn-sm" @click = "toggleProductModal('update');clickUpdateButton(item)">
                    編輯
                  </button>
                  <button type="button" class="btn btn-outline-danger btn-sm"
                  @click="toggledelProductModal(item,'delete')">
                    刪除
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div id="productModal" ref="productModal" class="modal fade" tabindex="-1" aria-labelledby="productModalLabel"
           aria-hidden="true">
        <div class="modal-dialog modal-xl">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 id="productModalLabel" class="modal-title">
                <span>新增產品</span>
              </h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="toggleProductModal('close')"></button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4">
                  <div class="mb-2">
                    <div class="mb-3">
                      <label for="imageUrl" class="form-label">主要圖片</label>
                      <input type="text" class="form-control"
                             placeholder="請輸入圖片連結" v-model="currentProduct.imageUrl">
                    </div>
                    <img class="img-fluid" :src="currentProduct.imageUrl" alt="">
                  </div>
                  <h5>多圖新增</h5>
                  <div class="mb-2" v-for="(image,index) in currentProduct.imagesUrl"
                  :key = "index">
                    <div class="mb-3">
                      <label for="imageUrl" class="form-label">圖片網址</label>
                      <input type="text" class="form-control"
                             placeholder="請輸入圖片連結" v-model="currentProduct.imagesUrl[index]">
                    </div>
                    <img class="img-fluid" :src="image" alt="">
                  </div>
                  <div>
                    <button class="btn btn-outline-primary btn-sm d-block w-100 mb-3"
                    @click = "addImage"
                    v-if="currentProduct.imagesUrl === undefined || currentProduct.imagesUrl.length < 5">
                      新增圖片
                    </button>
                  </div>
                  <div>
                    <button class="btn btn-outline-danger btn-sm d-block w-100"
                    @click = "removeImage"
                    v-if="currentProduct.imagesUrl !== undefined && currentProduct.imagesUrl.length !== 0">
                      刪除圖片
                    </button>
                  </div>
                </div>
                <div class="col-sm-8">
                  <div class="mb-3">
                    <label for="title" class="form-label">標題</label>
                    <input id="title" type="text" class="form-control" placeholder="請輸入標題" v-model="currentProduct.title">
                  </div>

                  <div class="row">
                    <div class="mb-3 col-md-6">
                      <label for="category" class="form-label">分類</label>
                      <input id="category" type="text" class="form-control"
                      v-model="currentProduct.category"
                      placeholder="請輸入分類">
                    </div>
                    <div class="mb-3 col-md-6">
                      <label for="price" class="form-label">單位</label>
                      <input id="unit" type="text" class="form-control" placeholder="請輸入單位" v-model="currentProduct.unit">
                    </div>
                  </div>

                  <div class="row">
                    <div class="mb-3 col-md-6">
                      <label for="origin_price" class="form-label">原價</label>
                      <input id="origin_price" type="number" min="0" class="form-control" placeholder="請輸入原價" v-model.number="currentProduct.origin_price">
                    </div>
                    <div class="mb-3 col-md-6">
                      <label for="price" class="form-label">售價</label>
                      <input id="price" type="number" min="0" class="form-control"
                      placeholder="請輸入售價" v-model.number="currentProduct.price">
                    </div>
                  </div>
                  <hr>

                  <div class="mb-3">
                    <label for="description" class="form-label">產品描述</label>
                    <textarea id="description" type="text" class="form-control"
                    placeholder="請輸入產品描述" v-model="currentProduct.description">
                    </textarea>
                  </div>
                  <div class="mb-3">
                    <label for="content" class="form-label">說明內容</label>
                    <textarea id="description" type="text" class="form-control"
                    placeholder="請輸入說明內容" v-model="currentProduct.content">
                    </textarea>
                  </div>
                  <div class="mb-3">
                    <div class="form-check">
                      <input id="is_enabled" class="form-check-input" type="checkbox"
                      :true-value="1" :false-value="0"
                      v-model.number="currentProduct.is_enabled">
                      <label class="form-check-label" for="is_enabled">是否啟用</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal" @click="toggleProductModal('close')">
                取消
              </button>
              <button type="button" class="btn btn-primary" @click="action === 'create'? createProduct() : updateProduct()">
                確認
              </button>
            </div>
          </div>
        </div>
      </div>
      <div id="delProductModal" ref="delProductModal" class="modal fade" tabindex="-1"
           aria-labelledby="delProductModalLabel" aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
              <h5 id="delProductModalLabel" class="modal-title">
                <span>刪除產品</span>
              </h5>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="toggledelProductModal('','close')"></button>
            </div>
            <div class="modal-body">
              是否刪除
              <strong class="text-danger">{{currentProduct.title}}</strong> 商品(刪除後將無法恢復)。
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal"
              @click="toggledelProductModal('','close')">
                取消
              </button>
              <button type="button" class="btn btn-danger"
              @click = "deleteProduct">
                確認刪除
              </button>
            </div>
          </div>
        </div>
      </div>
</template>

<script>
import { Modal } from 'bootstrap'
export default {
  data () {
    return {
      products: [],
      currentProduct: {},
      action: '',
      productModal: '',
      delProductModal: ''
    }
  },
  methods: {
    getData () {
      this.$http.get(`${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/products/all`)
        .then(res => {
          this.products = res.data.products
        })
    },
    toggleProductModal (action) {
      if (action === 'create') {
        this.currentProduct = {}
        this.productModal.show()
        this.action = 'create'
      } else if (action === 'update') {
        this.productModal.show()
        this.action = 'update'
      } else {
        this.currentProduct = {}
        this.action = ''
        this.productModal.hide()
      }
    },
    toggledelProductModal (item, action) {
      if (action === 'delete') {
        this.currentProduct = JSON.parse(JSON.stringify(item))
        this.delProductModal.show()
      } else {
        this.currentProduct = {}
        this.delProductModal.hide()
      }
    },
    createProduct () {
      const productInfo = { data: this.currentProduct }
      const url = `${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/admin/product`
      this.$http.post(url, productInfo)
        .then((res) => {
          alert(res.data.message)
          this.toggleProductModal('close')
          this.getData()
        })
        .catch((err) => {
          alert(err.data.message)
        })
    },
    clickUpdateButton (item) {
      this.currentProduct = JSON.parse(JSON.stringify(item))
    },
    updateProduct () {
      const productInfo = { data: this.currentProduct }
      const id = this.currentProduct.id
      const url = `${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/admin/product/${id}`
      this.$http.put(url, productInfo)
        .then((res) => {
          alert(res.data.message)
          this.toggleProductModal('close')
          this.getData()
        })
        .catch((err) => {
          alert(err.data.message)
        })
    },
    addImage () {
      if (this.currentProduct.imagesUrl === undefined) {
        this.currentProduct.imagesUrl = []
        this.currentProduct.imagesUrl.push('')
      } else {
        this.currentProduct.imagesUrl.push('')
      }
    },
    removeImage () {
      this.currentProduct.imagesUrl.pop()
    },
    deleteProduct () {
      const id = this.currentProduct.id
      const url = `${process.env.VUE_APP_API}v2/api/${process.env.VUE_APP_PATH}/admin/product/${id}`
      this.$http.delete(url)
        .then((res) => {
          alert(res.data.message)
          this.toggledelProductModal('', 'close')
          this.getData()
        })
        .catch((err) => {
          alert(err.data.message)
        })
    },
    checkLog () {
      const url = `${process.env.VUE_APP_API}v2/api/user/check`
      this.$http.post(url)
        .then(() => {
          this.getData()
        })
        .catch((err) => {
          alert(err.response.data.message)
          this.$router.push('/login')
        })
    }
  },
  mounted () {
    this.productModal = new Modal(document.querySelector('#productModal'))
    this.delProductModal = new Modal(document.querySelector('#delProductModal'))
    const token = document.cookie.replace(/(?:(?:^|.*;\s*)token\s*=\s*([^;]*).*$)|^.*$/, '$1')
    this.$http.defaults.headers.common.Authorization = token
    this.checkLog()
  }
}
</script>
